import { createStyles } from '@app/react/common/hooks/createStyles'
import { useLang, useLangAria } from '@app/services/config/hooks'
import interpolate from '@instacart/enterprise-services/lang/interpolate'
import { ShoppingListItem } from '@app/types/shoppingList'
import { Button } from '@instacart/cocktail'
import { isRxProduct, isProductAvailable } from '@instacart/enterprise-services/product/utils'
import { useState, useEffect } from 'react'
import { spacing } from '@instacart/ids-core'

import OutOfStockMessage from './OutOfStockMessage'
import ModifyQuantity from './ModifyQuantity'

interface Props {
  item: ShoppingListItem
  updateQuantity: (newQuantity: number) => void
  updateComment: (newComment: string) => void
}

const useStyles = createStyles(theme => ({
  actions: {
    alignItems: 'center',
    display: 'flex',
    justifyContent: 'space-between',
  },
  cancelButton: {
    marginLeft: spacing.s4,
  },
  comment: {
    padding: `${spacing.s8}px 0`,
    paddingLeft: spacing.s16,
  },
  commentButton: {
    color: theme.colors.systemGrayscale50,
    fontSize: theme.typography.bodySmall1.fontSize,
  },
  commentText: {
    ...theme.typography.bodySmall2,
    fontStyle: 'italic',
    padding: `0px ${spacing.s16}px`,
  },
  commentTextArea: {
    borderRadius: theme.radius.r8,
    fontSize: theme.typography.bodyMedium1.fontSize,
    lineHeight: 1.5,
    minHeight: 100,
    padding: `${spacing.s8}px ${spacing.s16}px`,
    width: '100%',
  },
  removeButton: {
    color: theme.colors.systemGrayscale50,
    fontSize: theme.typography.bodySmall1.fontSize,
  },
  root: {
    color: theme.colors.systemGrayscale50,
  },
}))

const ItemActions = ({ item, updateQuantity, updateComment }: Props) => {
  const css = useStyles()
  const lang = useLang()
  const langAria = useLangAria()

  const [editingComment, setEditingComment] = useState(false)
  const [newComment, setNewComment] = useState(item.comment || '')

  useEffect(() => {
    setNewComment(item.comment || '')
  }, [item.comment])

  const handleUpdate = () => {
    if (newComment !== item.comment) {
      updateComment(newComment)
    }
    setEditingComment(false)
  }

  const buttonComp = (
    <Button
      variant="link"
      iconBefore="icon-edit-list"
      iconSize="small"
      css={css.commentButton}
      onClick={() => setEditingComment(true)}
      data-test="item-add-comment-button"
      aria-label={interpolate(langAria.addNoteTo, { item: item.store_product.name })}
    >
      {newComment ? lang.editNote : lang.addNote}
    </Button>
  )

  const onEnterPress = (e: React.KeyboardEvent<HTMLTextAreaElement>) => {
    if (e.key === 'Enter' && e.shiftKey === false) {
      handleUpdate()
    }
  }

  const inputComp = (
    <div css={css.comment}>
      <textarea
        defaultValue={newComment}
        onChange={e => setNewComment(e.target.value)}
        onKeyDown={onEnterPress}
        css={css.commentTextArea}
        data-test="item-comment-input"
      />
      <Button onClick={handleUpdate} data-test="item-save-note-button" size="small">
        {lang.saveNote}
      </Button>
      <Button
        css={css.cancelButton}
        onClick={() => setEditingComment(false)}
        variant="outline"
        size="small"
        data-test="item-cancel-note-button"
      >
        {lang.cancel}
      </Button>
    </div>
  )

  const isUnavailable = !isProductAvailable(item.store_product)
  const showCommentAndModifyQuantity = !isUnavailable && !isRxProduct(item)

  return !editingComment ? (
    <div css={css.root}>
      {item.comment && (
        <span css={css.commentText} aria-label={langAria.itemNotes}>
          {item.comment}
        </span>
      )}
      <div css={css.actions}>
        <Button
          variant="link"
          iconBefore="icon-trash"
          iconSize="small"
          css={css.removeButton}
          onClick={() => updateQuantity(0)}
          data-test="remove-item-button"
          aria-label={interpolate(langAria.removeItemFromCart, { item: item.store_product.name })}
        >
          {lang.remove}
        </Button>
        {showCommentAndModifyQuantity && buttonComp}
        {showCommentAndModifyQuantity && (
          <ModifyQuantity
            quantity={item.quantity}
            orderingParameters={item.store_product.ordering_parameters}
            onUpdate={updateQuantity}
          />
        )}
        {isUnavailable && <OutOfStockMessage />}
      </div>
    </div>
  ) : (
    inputComp
  )
}

export default ItemActions
