import { createStyles } from '@app/react/common/hooks/createStyles'
import { spacing } from '@instacart/ids-core'
import { ContextCart } from '@app/types/cart'
import { useLang } from '@app/services/config/hooks'
import loadAssetImage from '@app/utils/loadAssetImage'
import { formatCurrency } from '@app/utils/currency'

interface Props {
  store: ContextCart
}

const useStyles = createStyles(theme => ({
  cartMin: {
    background: theme.colors.systemGrayscale10,
    borderRadius: theme.radius.r4,
    marginTop: spacing.s8,
    padding: `${spacing.s4}px ${spacing.s8}px`,
    width: '100%',
    ...theme.typography.bodySmall2,
  },
  logo: {
    background: theme.colors.systemGrayscale10,
    borderRadius: '50%',
    boxShadow: '0 0 0 1px rgb(0 0 0 / 15%)',
    flex: 0,
    height: spacing.s48,
    width: spacing.s48,
  },
  root: {
    alignItems: 'center',
    backgroundColor: theme.colors.systemGrayscale20,
    color: theme.colors.systemGrayscale70,
    display: 'flex',
    flexWrap: 'wrap',
    margin: 0,
    padding: spacing.s8,
    ...theme.typography.bodyMedium2,
  },
  title: {
    flex: 1,
    paddingLeft: spacing.s4,
    width: '100%',
  },
  total: {
    flex: 0,
    width: '100%',
  },
}))

const StoreHeader = ({ store }: Props) => {
  const css = useStyles()
  const lang = useLang()

  const logo = store.banner?.logoImageUrl
    ? loadAssetImage(store.banner.logoImageUrl)
    : loadAssetImage('images/header-logo.svg')

  return (
    <h2 css={css.root}>
      <img css={css.logo} src={logo} alt={lang.clientName} />
      <div css={css.title}>{store?.banner?.name ? store.banner.name : lang.clientName}</div>
      <div css={css.total}>{formatCurrency(store.product_total)}</div>
      {!!store.cartMinMsg && (
        <div css={css.cartMin} dangerouslySetInnerHTML={{ __html: store.cartMinMsg }} />
      )}
    </h2>
  )
}

export default StoreHeader
