import { useState, useEffect } from 'react'
import { createStyles } from '@app/react/common/hooks/createStyles'
import { AddIcon, SubtractIcon, TrashIcon, spacing } from '@instacart/ids-core'
import { OrderingParameters } from '@app/types/product'

interface Props {
  quantity: number
  onUpdate: (q: number) => void
  orderingParameters: OrderingParameters
}

const useStyles = createStyles(theme => ({
  icon: {},
  quantity: {
    flex: 1,
    textAlign: 'center',
  },
  root: {
    alignItems: 'center',
    border: `2px ${theme.colors.systemGrayscale20} solid`,
    borderRadius: theme.radius.r4,
    display: 'flex',
    fontSize: theme.typography.subtitleSmall.fontSize,
    height: spacing.s40,
    justifyContent: 'space-evenly',
    width: 81,
  },
  stepButton: {
    '&:hover': {
      background: theme.colors.systemGrayscale20,
    },
    background: theme.colors.systemGrayscale10,
    flex: 1,
    fontSize: theme.typography.subtitleLarge.fontSize,
    height: '100%',
  },
}))

const withDecimal = (num: number) => (Math.round(num * 100) / 100).toFixed(1)

const ModifyQuantity = ({ quantity, orderingParameters, onUpdate }: Props) => {
  const css = useStyles()
  const [newQuantity, setNewQuantity] = useState(quantity)
  const { increment, min, max } = orderingParameters || {}

  useEffect(() => {
    setNewQuantity(quantity)
  }, [quantity])

  const updateQuantity = (newVal: number) => {
    if (newVal >= 0 && newVal <= max) {
      setNewQuantity(newVal)
      if (newVal !== quantity) {
        onUpdate(newVal)
      }
    }
  }

  const incrementQuantity = (newIncrement: number) => {
    updateQuantity(newQuantity + newIncrement)
  }

  const quantityStepper = (
    <div css={css.root} data-test="item-modify-quantity">
      <button
        css={css.stepButton}
        onClick={() => incrementQuantity(-increment)}
        data-test="item-decrement-quantity-button"
      >
        {newQuantity === min ? <TrashIcon css={css.icon} /> : <SubtractIcon css={css.icon} />}
      </button>
      <span css={css.quantity}>{increment !== 1 ? withDecimal(newQuantity) : newQuantity}</span>
      <button
        css={css.stepButton}
        onClick={() => incrementQuantity(increment)}
        data-test="item-increment-quantity-button"
      >
        <AddIcon css={css.icon} />
      </button>
    </div>
  )

  return quantityStepper
}

export default ModifyQuantity
