import React, { memo, forwardRef } from 'react'
import PropTypes from 'prop-types'
import { Text, createStyles, ScreenReaderText, utils } from '@instacart/cocktail'

const useStyles = createStyles(
  {
    root: {},
    amount: {},
    perUnit: {},
    ebtText: {
      color: '#024D36',
      fontWeight: 'bold',
    },
    unavailable: {
      color: '#6f6f6f',
    },
  },
  { displayName: 'Price' }
)

const defaultLang = {
  quantitySeparator: 'for',
}

const Price = forwardRef((props, ref) => {
  const styles = useStyles(props)
  const {
    quantity,
    amount,
    unit,
    perUnit,
    suffix,
    prefix,
    i18n,
    styles: originalStyles,
    screenReaderLabel,
    showEbtLabel,
    isUnavailable = false,
    ...restProps
  } = props

  const lang = { ...defaultLang, ...i18n }
  const ebtText = 'EBT'

  return (
    <div ref={ref} css={[styles.root, isUnavailable && styles.unavailable]} {...restProps}>
      {prefix}
      {screenReaderLabel && <ScreenReaderText>{screenReaderLabel}</ScreenReaderText>}
      {quantity > 1 ? (
        <Text
          css={[styles.amount, isUnavailable && styles.unavailable]}
          component="span"
          variant="inherit"
          data-test="amount-multiple"
        >
          {quantity}
          {unit && unit !== 'ea' ? ` ${unit}` : null} {lang.quantitySeparator}{' '}
          {utils.formatCurrency(amount)}
        </Text>
      ) : (
        <span data-test="amount">
          <Text
            css={[styles.amount, isUnavailable && styles.unavailable]}
            component="span"
            variant="inherit"
          >
            {utils.formatCurrency(amount)}
          </Text>
          <Text component="span" variant="inherit">
            {unit ? ` / ${unit}` : null}
          </Text>
        </span>
      )}
      {perUnit ? (
        <Text
          data-test="per-unit-price"
          component="span"
          variant="inherit"
          css={[styles.perUnit, isUnavailable && styles.unavailable]}
        >
          {
            // eslint-disable-next-line react/jsx-no-literals
            ` (${utils.formatCurrency(perUnit.amount)}/${perUnit.type})`
          }
        </Text>
      ) : null}
      {suffix}
      {showEbtLabel && (
        <Text css={styles.ebtText} component="span" variant="inherit">
          {
            // eslint-disable-next-line react/jsx-no-literals
            `, ${ebtText}`
          }
        </Text>
      )}
    </div>
  )
})

Price.defaultProps = {
  quantity: 1,
  i18n: {},
  unit: 'ea',
}

Price.propTypes = {
  /** Flag for whether additional markup is required to convey strikethrough text to screenreaders  */
  screenReaderLabel: PropTypes.string,
  /** Custom emotion css object or class string for the containing element */
  className: PropTypes.oneOfType([PropTypes.object, PropTypes.string]),
  /** Custom style object for the containing element */
  // eslint-disable-next-line react/forbid-prop-types
  style: PropTypes.object,
  /** Custom style object for the containing element */
  // eslint-disable-next-line react/forbid-prop-types
  styles: PropTypes.object,
  /** Custom language strings */
  // eslint-disable-next-line react/forbid-prop-types
  i18n: PropTypes.object,
  /** Quantity of units */
  quantity: PropTypes.number,
  /** Amount to display */
  amount: PropTypes.number.isRequired,
  /** Unit of measure for the amount */
  unit: PropTypes.node,
  /** Per unit pricing to display */
  perUnit: PropTypes.shape({
    amount: PropTypes.number,
    type: PropTypes.node,
  }),
  /** Suffix the price with a string or node */
  suffix: PropTypes.node,
  /** Prefix the price with a string or node */
  prefix: PropTypes.node,
  /** Flag to show the EBT label on the mini cart. */
  showEbtLabel: PropTypes.bool,
  /** Whether the price should be faded if the item is unavaiable */
  isUnavailable: PropTypes.bool,
}

export default memo(Price)
