import { createStyles } from '@app/react/common/hooks/createStyles'
import { ShoppingListItem } from '@app/types/shoppingList'
import { useConfig } from '@app/services/config/hooks'
import { CartTotalsForCartTypeMap } from '@app/types/cart'
import { spacing } from '@instacart/ids-core'

import { categorizeItemsByDepartment, getDataByCartType } from './utils'
import MiniCartListItem from './MiniCartListItem'
import StoreHeader from './StoreHeader'

interface Props {
  items: ShoppingListItem[]
  onClick: () => void
  onItemUpdate: () => void
  obfuscatedRxItems: { [key: string]: string }
  cartTotals: CartTotalsForCartTypeMap
  isInstoreList: boolean
}

const useStyles = createStyles(theme => ({
  categoryHeader: {
    height: spacing.s40,
    ...theme.typography.bodyMedium2,
    alignItems: 'center',
    background: theme.colors.systemGrayscale10,
    borderBottom: `1px solid ${theme.colors.systemGrayscale20}`,
    borderTop: `1px solid ${theme.colors.systemGrayscale20}`,
    display: 'flex',
    margin: 0,
    padding: `0 ${spacing.s16}px`,
  },
  root: {
    color: theme.colors.systemGrayscale70,
  },
}))

const MiniCartList = ({
  items,
  onClick,
  onItemUpdate,
  obfuscatedRxItems,
  cartTotals,
  isInstoreList,
}: Props) => {
  const css = useStyles()
  const { showCategoryHeadersInSideCart, useMultiContextCart, multipleBanners } = useConfig()

  const itemList = (itemsArray: ShoppingListItem[]) =>
    itemsArray.map((item: ShoppingListItem) => {
      let nameOveride = ''
      if (
        obfuscatedRxItems &&
        Object.keys(obfuscatedRxItems).length > 0 &&
        Object.prototype.hasOwnProperty.call(obfuscatedRxItems, item.store_product.id)
      ) {
        nameOveride = obfuscatedRxItems[item.store_product.id]
      }
      return (
        !!item?.store_product?.id && (
          <MiniCartListItem
            item={item}
            onClick={onClick}
            key={item.store_product.id}
            onItemUpdate={onItemUpdate}
            nameOveride={nameOveride}
            isInstoreList={isInstoreList}
          />
        )
      )
    })

  const categorizedItemsList = (itemsArray: ShoppingListItem[]) => {
    const categorizedItems = categorizeItemsByDepartment(itemsArray)

    return Object.keys(categorizedItems).map(category => (
      <div key={category}>
        <h2 css={css.categoryHeader}>{category}</h2>
        {itemList(categorizedItems[category])}
      </div>
    ))
  }

  const itemListByStore = (
    itemsArray: ShoppingListItem[],
    cartTotalsObject: CartTotalsForCartTypeMap
  ) => {
    const dataByCartType = getDataByCartType(itemsArray, cartTotalsObject, multipleBanners.banners)
    return Object.entries(dataByCartType).map(([key, store]) => (
      <div key={key}>
        <StoreHeader store={store} />
        {showCategoryHeadersInSideCart ? categorizedItemsList(store.items) : itemList(store.items)}
      </div>
    ))
  }

  return (
    !!items && (
      <ul css={css.root}>
        {useMultiContextCart
          ? itemListByStore(items, cartTotals)
          : showCategoryHeadersInSideCart
          ? categorizedItemsList(items)
          : itemList(items)}
      </ul>
    )
  )
}

export default MiniCartList
