import { createStyles } from '@app/react/common/hooks/createStyles'
import { useState } from 'react'
import { ShoppingListItem } from '@app/types/shoppingList'
import { ProductPrices } from '@instacart/enterprise-ui-product-price'
import { formatCurrency } from '@app/utils/currency'
import {
  cart as enterpriseCartService,
  list as enterpriseListService,
} from '@instacart/enterprise-services'
import { spacing } from '@instacart/ids-core'
import { isProductAvailable } from '@instacart/enterprise-services/product/utils'
import { getItemDisplayName } from '@instacart/enterprise-services/shopping-list/_common/utils'
import { useConfig } from '@app/services/config/hooks'

import { Button } from '@instacart/cocktail'
import ProductImage from './ProductImage'
import ItemActions from './ItemActions'

interface Props {
  item: ShoppingListItem
  onClick: (item: ShoppingListItem) => void
  onItemUpdate: () => void
  nameOveride: string
  isInstoreList: boolean
}

const useStyles = createStyles(theme => ({
  details: {
    flex: '1',
  },
  itemName: {
    color: theme.colors.systemGrayscale70,
    textAlign: 'left',
  },
  itemSubtotal: {
    fontSize: theme.typography.bodyMedium1.fontSize,
    fontWeight: theme.typography.bodyMedium1.fontWeight,
  },
  productImage: {
    minWidth: spacing.s40,
  },
  root: {
    borderBottom: `1px solid ${theme.colors.systemGrayscale20}`,
    color: theme.colors.systemGrayscale70,
    display: 'flex',
    padding: `${spacing.s8}px ${spacing.s12}px`,
  },
  row1: {
    alignItems: 'center',
    display: 'flex',
    justifyContent: 'space-between',
  },
  row2: {
    display: 'flex',
    justifyContent: 'space-between',
    padding: `0px ${spacing.s16}px`,
  },
  unavailable: {
    color: theme.colors.systemGrayscale50,
  },
}))

const MiniCartListItem = ({
  item,
  onClick,
  onItemUpdate,
  nameOveride,
  isInstoreList = false,
  ...restProps
}: Props) => {
  const css = useStyles()
  const config = useConfig()

  // This is need so we can hide an item with a 0 quantity while it is updating
  const [isVisible, setIsVisible] = useState(true)

  const service = isInstoreList ? enterpriseListService : enterpriseCartService

  const updateQuantity = (newQuantity: number) => {
    if (newQuantity === 0) setIsVisible(false)
    service.actions.updateItemQuantity(item.store_product, newQuantity)
    onItemUpdate()
  }

  const updateComment = (newComment: string) => {
    service.actions.updateItemComment(item, newComment)
    onItemUpdate()
  }

  const isUnavailable = !isProductAvailable(item.store_product)

  return isVisible ? (
    <li
      css={[css.root, isUnavailable && css.unavailable]}
      data-test="mini-cart-item"
      id={`mini-cart-item-${item.id}`}
    >
      <div css={css.productImage}>
        <ProductImage item={item} onClick={onClick} isUnavailable={isUnavailable} />
      </div>

      <div css={css.details}>
        <div css={css.row1}>
          <Button
            variant="link"
            css={[css.itemName, isUnavailable && css.unavailable]}
            onClick={() => onClick(item)}
          >
            {nameOveride || getItemDisplayName(item, config.hideSizeStringSideCart)}
          </Button>
          <div css={css.itemSubtotal} data-test="cart-item-total">
            {formatCurrency(item.subtotal)}
          </div>
        </div>
        <div css={css.row2}>
          <ProductPrices item={item} {...restProps} />
        </div>
        <ItemActions item={item} updateQuantity={updateQuantity} updateComment={updateComment} />
      </div>
    </li>
  ) : (
    <></>
  )
}

export default MiniCartListItem
