import { createStyles } from '@app/react/common/hooks/createStyles'
import { useLang } from '@app/services/config/hooks'
import { spacing, InformationIcon } from '@instacart/ids-core'
import { Tooltip, TooltipReference, useTooltipState } from 'reakit/Tooltip'

const useStyles = createStyles(theme => ({
  OosMessage: {
    ...theme.typography.bodyMedium2,
  },
  icon: {
    fill: theme.colors.systemGrayscale50,
    verticalAlign: 'text-top',
  },
  root: {},
  tooltip: {
    ...theme.typography.bodySmall2,
    background: theme.colors.systemGrayscale00,
    border: `1px solid ${theme.colors.systemGrayscale20}`,
    borderRadius: theme.radius.r4,
    boxShadow: '0 0 4px rgb(0 0 0 / 15%)',
    padding: `${spacing.s4}px ${spacing.s8}px`,
    zIndex: 9000,
  },
}))

const OutOfStockMessage = () => {
  const css = useStyles()
  const lang = useLang()
  const tooltip = useTooltipState()

  return (
    <div css={css.root}>
      <TooltipReference {...tooltip}>
        <span css={css.OosMessage}>
          {lang.outOfStock} <InformationIcon aria-hidden="true" css={css.icon} />
        </span>
      </TooltipReference>
      <Tooltip {...tooltip} css={css.tooltip}>
        {lang.outOfStockDescription}
      </Tooltip>
    </div>
  )
}

export default OutOfStockMessage
