import React from 'react'
import { ShoppingListItem } from '@app/types/shoppingList'
import { useLang, useConfig } from '@app/services/config/hooks'
import { shouldShowUOMSelector } from '@app/services/product/selectors'
import { isEbtProduct, isProductAvailable } from '@instacart/enterprise-services/product/utils'
import { loyalty, cart as enterpriseCartService } from '@instacart/enterprise-services'
import { getSalePrice } from '@instacart/enterprise-services/shopping-list/_common/utils'

import ProductPrice from './ProductPrice'

type Props = {
  item: ShoppingListItem
  hasEligibleEbtCard?: boolean
}

const ProductPrices: React.FC<Props> = props => {
  const { item, hasEligibleEbtCard = false, ...restProps } = props

  const lang = useLang()
  const config = useConfig()
  const i18n = {
    loyaltyTooltip: lang.loyaltyPricing.priceTooltip,
    loyaltyBaseSuffix: lang.or,
  }

  const shouldShowSalePrice =
    config.useSalePricingForOffersInSideCartCostcoOnly &&
    item.quantity !== 0 &&
    enterpriseCartService.selectors.hasItemOffer(item)

  const salePriceOverride: number = shouldShowSalePrice ? getSalePrice(item) : 0

  return (
    <ProductPrice
      i18n={i18n}
      unitPrice={item.unit_price}
      product={item?.store_product}
      showUom={shouldShowUOMSelector.resultFunc(item.store_product, config)}
      showEbtLabel={hasEligibleEbtCard && isEbtProduct(item)}
      loyaltyPricingEnabled={!!(item.store_product.loyalty_price && config.enableLoyaltyPricing)}
      hasLoyaltyCard={loyalty.selectors.isLoyaltyMember()}
      salePriceOverride={salePriceOverride}
      isUnavailable={!isProductAvailable(item.store_product)}
      {...restProps}
    />
  )
}

export default ProductPrices
