import { createStyles } from '@app/react/common/hooks/createStyles'
import { ShoppingListItem } from '@app/types/shoppingList'
import { getProductImageURL } from '@app/utils/images/productImages'
import { spacing } from '@instacart/ids-core'

import { ItemButton } from '@app/react/ids/enterprise/atoms/ItemButton'
import { ItemB } from '@app/react/ids/enterprise/molecules/ItemB'

const { ItemImage } = ItemB
const IMG_SIZE = 'small'

interface Props {
  item: ShoppingListItem
  onClick?: (item: ShoppingListItem) => void
  isUnavailable: boolean
}

const useStyles = createStyles(() => ({
  root: {
    maxHeight: spacing.s40,
    maxWidth: spacing.s40,
  },
  unavailable: {
    opacity: 0.3,
  },
}))

const ProductImage = ({ item, onClick, isUnavailable = false }: Props) => {
  const css = useStyles()

  const imgComp = (
    <ItemImage src={getProductImageURL(item, IMG_SIZE)} alt={item.store_product.name} />
  )

  return (
    <div css={[css.root, isUnavailable && css.unavailable]}>
      {onClick && !item.product_config ? (
        <ItemButton
          onClick={() => onClick(item)}
          aria-hidden="true"
          tabIndex={-1}
          data-test="mini-cart-item-link"
        >
          {imgComp}
        </ItemButton>
      ) : (
        imgComp
      )}
    </div>
  )
}

export default ProductImage
