import React, { Fragment, forwardRef } from 'react'
import PropTypes from 'prop-types'
import { Tooltip, Text, createStyles } from '@instacart/cocktail'
import Price from './Price'

export const propTypes = {
  /** Custom language strings */
  // eslint-disable-next-line react/forbid-prop-types
  i18n: PropTypes.object,
  /** Render base and promo prices inline */
  inline: PropTypes.bool,
  /** Display a label above base price */
  label: PropTypes.string,
  /** Flag to decide if we show loyalty pricing */
  loyaltyPricingEnabled: PropTypes.bool,
  /** Flag that denotes if the user has a loyalty card */
  hasLoyaltyCard: PropTypes.bool,
  /** Class name for the loyalty icon */
  loyaltyIcon: PropTypes.string,
  /** Price for configurable products */
  unitPrice: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
  /** Custom style object for the containing element */
  // eslint-disable-next-line react/forbid-prop-types
  styles: PropTypes.object,
  /** A unata product object */
  // eslint-disable-next-line react/forbid-prop-types
  product: PropTypes.object,
  /** Flag to denote if we show the unit-of-measurement */
  showUom: PropTypes.bool,
  /** Flag to show the EBT label on the mini cart. */
  showEbtLabel: PropTypes.bool,
  /** sale pricing amount to use instead of offers (for costco only) */
  salePriceOverride: PropTypes.number,
  /** Whether the product is unavailable or not */
  isUnavailable: PropTypes.bool,
}

const useStyles = createStyles(
  tokens => ({
    root: {
      color: tokens.color.text.secondary,
      fontSize: tokens.size.font.caption,
      lineHeight: tokens.size.lineHeight.body,
    },
    strikethrough: {
      textDecoration: 'line-through',
    },
    amount: {
      color: tokens.color.text.primary,
      fontSize: tokens.size.font.body,
      fontWeight: tokens.font.weight.bold,
    },
    promo: {
      color: tokens.color.brand.highlight.regular,
    },
    label: {},
    perUnit: {},
    loyaltyRequired: {
      color: tokens.color.brand.highlight.regular,
    },
    loyaltyIcon: {
      color: tokens.color.brand.highlight.regular,
      marginRight: '0.25em',
    },
    inline: {
      marginLeft: -tokens.size.spacing.xxxSmall,
      marginRight: -tokens.size.spacing.xxxSmall,
      '> *': {
        marginLeft: tokens.size.spacing.xxxSmall,
        marginRight: tokens.size.spacing.xxxSmall,
        display: 'inline-block',
      },
    },
  }),
  { displayName: 'ProductPrice' }
)

const defaultLang = {
  label: 'Price',
  loyaltyTooltip: 'Exclusive deals for loyalty members',
  loyaltyBaseSuffix: 'or',
  screenReaderLabelBase: 'Original price:',
  screenReaderLabelSale: 'Current price:',
}

const ProductPrice = forwardRef((props, ref) => {
  const styles = useStyles(props)
  const {
    i18n,
    hasLoyaltyCard,
    loyaltyIcon,
    inline,
    label,
    showEbtLabel,
    showUom,
    unitPrice,
    styles: originalStyles,
    loyaltyPricingEnabled,
    product,
    salePriceOverride,
    isUnavailable = false,
    ...restProps
  } = props

  const lang = { ...defaultLang, ...i18n }

  const showConfigurablePrice =
    product.product_config && product.product_config.has_pricing && !!unitPrice
  const showLoyaltyPrice = loyaltyPricingEnabled && product.loyalty_price
  const showSalePrice = product.sale_price && !showLoyaltyPrice
  const showSalePriceOverride = salePriceOverride && !showLoyaltyPrice
  const showUomPrice = showUom && product.uom_price.price && !unitPrice

  let perUnit
  if (showUomPrice) {
    perUnit = {
      amount: product.uom_price.price,
      type: product.uom_price.uom,
    }
  }

  return (
    <div ref={ref} css={[styles.root, inline && styles.inline]} {...restProps}>
      {showSalePrice || showSalePriceOverride ? (
        <Fragment>
          <Price
            styles={{ root: styles.strikethrough }}
            amount={product.base_price}
            quantity={product.base_quantity}
            unit={product.display_uom}
            data-test="sale-base-price"
            screenReaderLabel={lang.screenReaderLabelBase}
          />
          <Price
            styles={{
              amount: [styles.amount, styles.promo],
              perUnit: styles.perUnit,
            }}
            amount={salePriceOverride || product.sale_price}
            quantity={product.sale_quantity}
            unit={product.display_uom}
            perUnit={perUnit}
            data-test="sale-price"
            screenReaderLabel={lang.screenReaderLabelSale}
            showEbtLabel={showEbtLabel}
            isUnavailable={isUnavailable}
          />
        </Fragment>
      ) : showLoyaltyPrice ? (
        <Fragment>
          <Price
            suffix={!hasLoyaltyCard && ` ${lang.loyaltyBaseSuffix}`}
            styles={{
              root: hasLoyaltyCard ? styles.strikethrough : {},
            }}
            amount={product.base_price}
            quantity={product.base_quantity}
            unit={product.display_uom}
            data-test="loyalty-base-price"
            screenReaderLabel={lang.screenReaderLabelBase}
          />
          <Tooltip content={lang.loyaltyTooltip}>
            <Price
              tabIndex="0"
              prefix={
                loyaltyIcon && (
                  <i className={loyaltyIcon} css={styles.loyaltyIcon} aria-hidden="true" />
                )
              }
              styles={{
                amount: [styles.amount, styles.promo, !hasLoyaltyCard && styles.loyaltyRequired],
                perUnit: styles.perUnit,
              }}
              amount={product.loyalty_price}
              quantity={product.loyalty_quantity}
              unit={product.display_uom}
              perUnit={perUnit}
              data-test="loyalty-price"
              screenReaderLabel={lang.screenReaderLabelSale}
              showEbtLabel={showEbtLabel}
              isUnavailable={isUnavailable}
            />
          </Tooltip>
        </Fragment>
      ) : (
        <Fragment>
          {label && (
            <Text variant="inherit" data-test="base-price-label" css={styles.label}>
              {label}
            </Text>
          )}
          <Price
            amount={showConfigurablePrice ? Number(unitPrice) : product.base_price}
            quantity={product.base_quantity}
            unit={product.display_uom}
            perUnit={perUnit}
            styles={{
              amount: styles.amount,
              perUnit: styles.perUnit,
            }}
            data-test="base-price"
            showEbtLabel={showEbtLabel}
            isUnavailable={isUnavailable}
          />
        </Fragment>
      )}
    </div>
  )
})

ProductPrice.defaultProps = {
  i18n: {},
  inline: false,
  loyaltyIcon: 'icon-loyalty-badge',
}

ProductPrice.propTypes = propTypes

export default ProductPrice
